body {
  font-family: sans-serif;
  margin: -2px;
  background: #63856a; /* Old browsers */
  background: -moz-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* FF3.6+ */
  background: -webkit-gradient(
    radial,
    center center,
    0px,
    center center,
    100%,
    color-stop(1%, #63856a),
    color-stop(100%, #3c5a40)
  ); /* Chrome,Safari4+ */
  background: -webkit-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* Chrome10+,Safari5.1+ */
  background: -o-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* Opera 12+ */
  background: -ms-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* IE10+ */
  background: radial-gradient(
    ellipse at center,
    #63856a 1%,
    #3c5a40 100%
  ); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#63856a', endColorstr='#3c5a40',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.header {
  padding: 8px;
  border-bottom: 1px solid rgb(205, 206, 187);
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  color: rgb(205, 206, 187);
}

.content {
  padding: 8px;
}

html,
body {
  min-height: 100%;
}
